import React, { Component, useEffect } from 'react';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import { url } from '../../../defaults/default';
import axios from 'axios';
import { IconButton, Fab, AppBar, Toolbar } from '@material-ui/core';
import InputMask from 'react-input-mask';
import CloseIcon from '@material-ui/icons/Close';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import AddIcon from '@material-ui/icons/Add';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Cookies from 'universal-cookie';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import { setAlert } from './../../../actions/alert';
import { connect } from 'react-redux';
import { faTruckMoving } from '@fortawesome/free-solid-svg-icons';
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import PostAddIcon from '@material-ui/icons/PostAdd';
import Drawer from '@material-ui/core/Drawer';
axios.defaults.baseURL = url;

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    padding: theme.spacing(5, 30),
  },
  myRoot: {
    width: '100%',

    padding: theme.spacing(5, 10),
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  toolbar: theme.mixins.toolbar,
  resetContainer: {
    padding: theme.spacing(3),
  },
}));
const VerticalLinearStepper = (props) => {
  const classes = useStyles();
  const [state, setState] = React.useState({
    open: false,
    right: false,
  });
  const handleClose = (e) => {
    setState({ ...state, open: false, right: false });
  };
  const [formData, setFormData] = React.useState({
    driver: '',
    marque: '',
    model: '',
    observation: '',
    kilometrage: '',
    categorie: '',
    matricule: '',
    dateMiseEnService: '',
    numeroSerie: '',
    couleur: '',
    carburant: '',
  });

  useEffect(() => {
    const {
      driver,
      marque,
      model,
      observation,
      kilometrage,
      categorie,
      matricule,
      dateMiseEnService,
      numeroSerie,
      couleur,
      carburant,
    } = formData;
    async function fetchData() {
      await axios
        .get(url + '/api/vehicule/ById/' + props.identifier)
        .then((response) => {
          setFormData({
            ...formData,
            marque: response.data.marque,
            model: response.data.model,
            matricule: response.data.matricule,
            dateMiseEnService: response.data.dateMiseEnService,
            kilometrage: response.data.kilometrage,
            categorie: response.data.categorie,
            couleur: response.data.couleur,
            numeroSerie: response.data.numeroSerie,
            observation: response.data.observation,
            carburant: response.data.carburant,
            driver: response.data.driver,
          });
          console.log(formData);
        })
        .catch((error) => console.log(error.response));
    }
    fetchData();
  }, []);

  const {
    driver,
    marque,
    model,
    observation,
    kilometrage,
    categorie,
    matricule,
    dateMiseEnService,
    numeroSerie,
    couleur,
    carburant,
  } = formData;
  const addCategorie = (e) => {
    props.setAlert('Add new Categorie', 'info');
  };
  const addMarque = (e) => {
    toggleDrawer('right', true);

    // props.setAlert('Add new Brand', 'info');
  };
  const addModel = (e) => {
    props.setAlert('Add new Model', 'info');
  };
  const addColor = (e) => {
    props.setAlert('Add new Color', 'info');
  };
  const onChange = (e) =>
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  const send = async (e) => {
    e.preventDefault();
    const element = {
      driver,
      marque,
      model,
      observation,
      kilometrage,
      categorie,
      matricule,
      dateMiseEnService,
      numeroSerie,
      couleur,
      carburant,
    };

    try {
      const cookies = new Cookies();
      const body = JSON.stringify(element);
      const res = await axios
        .post('/api/vehicule/update/' + props.identifier, body, {
          headers: {
            'Content-Type': 'application/json;charset=UTF-8',
            'Access-Control-Allow-Origin': '*',
            // 'x-auth-token': cookies.get('token'),
          },
        })
        .then((response) => {
          props.sendData(
            marque,
            model,
            kilometrage,
            categorie,
            matricule,
            dateMiseEnService,
            props.index
          );
          props.setAlert('La mise a jours a ete faite avec Success', 'success');
        })
        .catch((error) => {
          console.log(error);
          props.setAlert("La mise a jours n'a pas eu lieu", 'error');
        });
    } catch (err) {
      console.log(err);
      props.setAlert("La mise a jours n'a pas eu lieu", 'error');
    }
  };
  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }
    setState({ ...state, open: true, right: open });
  };
  const list = () => (
    <div role='presentation' className={classes.myRoot}>
      <div className={classes.toolbar} />
      <Grid container spacing={2}>
        <Grid container justify='center'>
          <PostAddIcon style={{ width: 110, height: 110 }} color='primary' />
        </Grid>
        <Grid item xs={12} sm={12} lg={12}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Marque
            </InputLabel>
            <Select
              label='Marque'
              name='marque'
              value={marque}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='PEUGEOT'>PEUGEOT</MenuItem>
              <MenuItem value='RENAULT'>RENAULT</MenuItem>
              <MenuItem value='MERCEDES'>MERCEDES</MenuItem>
              <MenuItem value='SONACOM'>SONACOM</MenuItem>
              <MenuItem value='SHACMANN'>SHACMANN</MenuItem>
              <MenuItem value='SEAT'>SEAT</MenuItem>
              <MenuItem value='AUDI'>AUDI</MenuItem>
              <MenuItem value='FIAT'>FIAT</MenuItem>
            </Select>
          </FormControl>
        </Grid>

        <Grid item xs={12} sm={12} lg={12}>
          <TextField
            label='Numero de Serie'
            placeholder='Numero de Serie'
            helperText=''
            fullWidth
            margin='normal'
            variant='outlined'
            name='numeroSerie'
            value={numeroSerie}
            onChange={(e) => onChange(e)}
          />
        </Grid>
        <Grid item xs={12} sm={12} lg={6}>
          <Button
            variant='contained'
            size='large'
            fullWidth
            color='primary'
            onClick={(e) => handleClose(e)}
          >
            Valider
          </Button>
        </Grid>
        <Grid item xs={12} sm={12} lg={6}>
          <Button
            variant='contained'
            fullWidth
            size='large'
            color='secondary'
            onClick={toggleDrawer('right', false)}
          >
            Annuler
          </Button>
        </Grid>
      </Grid>
    </div>
  );
  return (
    <div className={classes.root} lg={5}>
      {/* <div className={classes.toolbar} /> */}
      <React.Fragment key='right'>
        <Drawer
          anchor='right'
          open={state['right']}
          onClose={toggleDrawer('right', false)}
        >
          {list()}
        </Drawer>
      </React.Fragment>
      <Grid container spacing={2}>
        <Grid container justify='center'>
          <FontAwesomeIcon icon={faTruckMoving} size='8x' color='#3f51b5' />
        </Grid>
        <Grid item xs={12} sm={12} lg={7}></Grid>
        <Grid item xs={12} sm={12} lg={5}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Chauffeur
            </InputLabel>
            <Select
              label='Chauffeur'
              name='driver'
              value={driver}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='O+'>O+</MenuItem>
              <MenuItem value='O-'>O-</MenuItem>
              <MenuItem value='A+'>A+</MenuItem>
            </Select>
          </FormControl>
        </Grid>

        <div className={classes.toolbar} />
        <Grid item xs={12} sm={12} lg={3}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Marque
            </InputLabel>
            <Select
              label='Marque'
              name='marque'
              value={marque}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='PEUGEOT'>PEUGEOT</MenuItem>
              <MenuItem value='RENAULT'>RENAULT</MenuItem>
              <MenuItem value='MERCEDES'>MERCEDES</MenuItem>
              <MenuItem value='SONACOM'>SONACOM</MenuItem>
              <MenuItem value='SHACMANN'>SHACMANN</MenuItem>
              <MenuItem value='SEAT'>SEAT</MenuItem>
              <MenuItem value='AUDI'>AUDI</MenuItem>
              <MenuItem value='FIAT'>FIAT</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={12} lg={1}>
          <IconButton
            edge='start'
            color='secondary'
            onClick={toggleDrawer('right', true)}
            margin='normal'
          >
            <ControlPointIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12} sm={12} lg={3}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Model
            </InputLabel>
            <Select
              label='Model'
              name='model'
              value={model}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='' disabled>
                <em>Choisissez un model</em>
              </MenuItem>
              <MenuItem value='206'>206</MenuItem>
              <MenuItem value='207'>207</MenuItem>
              <MenuItem value='c220'>c220</MenuItem>
              <MenuItem value='IBIZA'>IBIZA</MenuItem>
              <MenuItem value='LEAON'>LEAON</MenuItem>
              <MenuItem value='CLIO4'>CLIO4</MenuItem>
              <MenuItem value='FLUENCE'>FLUENCE</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={12} lg={1}>
          <IconButton
            edge='start'
            margin='normal'
            onClick={addModel}
            color='secondary'
          >
            <ControlPointIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12} sm={12} lg={3}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Categorie
            </InputLabel>
            <Select
              label='Categorie'
              name='categorie'
              value={categorie}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='Remorque'>Remorque</MenuItem>
              <MenuItem value='10Tone'>10Tone</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={12} lg={1}>
          <IconButton
            edge='start'
            color='secondary'
            margin='normal'
            onClick={addCategorie}
          >
            <ControlPointIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12} sm={12} lg={4}>
          <InputMask
            mask='9999 999 99'
            value={matricule}
            onChange={(e) => onChange(e)}
            maskChar='_'
          >
            {() => (
              <TextField
                label='Matricule'
                placeholder='Matricule'
                helperText=''
                fullWidth
                name='matricule'
                margin='normal'
                variant='outlined'
              />
            )}
          </InputMask>
        </Grid>
        <Grid item xs={12} sm={4} lg={4}>
          <TextField
            label='Numero de Serie'
            placeholder='Numero de Serie'
            helperText=''
            fullWidth
            margin='normal'
            variant='outlined'
            name='numeroSerie'
            value={numeroSerie}
            onChange={(e) => onChange(e)}
          />
        </Grid>
        <Grid item xs={12} sm={12} lg={4}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Carburant
            </InputLabel>
            <Select
              label='Carburant'
              name='carburant'
              value={carburant}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='Diesel'>Diesel</MenuItem>
              <MenuItem value='Essence'>Essence</MenuItem>
              <MenuItem value='Sans Plomb'>Sans Plomb</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={12} lg={3}>
          <FormControl
            variant='outlined'
            margin='normal'
            className={classes.formControl}
            style={{ minWidth: '100%' }}
          >
            <InputLabel id='demo-simple-select-outlined-label'>
              Couleur
            </InputLabel>
            <Select
              label='Couleur'
              name='couleur'
              value={couleur}
              onChange={(e) => onChange(e)}
            >
              <MenuItem value='Noire'>Noire</MenuItem>
              <MenuItem value='Blanc'>Blanc</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={12} lg={1}>
          <IconButton
            edge='start'
            onClick={addColor}
            color='secondary'
            margin='normal'
          >
            <ControlPointIcon />
          </IconButton>
        </Grid>
        <Grid item xs={12} sm={6} md={6} lg={4}>
          <TextField
            placeholder='Date Mise en Service'
            margin='normal'
            helperText='Date Mise en Service'
            fullWidth
            type='date'
            name='dateMiseEnService'
            value={dateMiseEnService}
            onChange={(e) => onChange(e)}
            variant='outlined'
          />
        </Grid>
        <Grid item xs={12} sm={12} lg={4}>
          <TextField
            label='Kilomertage'
            placeholder='Kilomertage'
            helperText=''
            fullWidth
            type='number'
            margin='normal'
            variant='outlined'
            name='kilometrage'
            value={kilometrage}
            onChange={(e) => onChange(e)}
          />
        </Grid>

        <Grid item xs={12} sm={12} lg={12}>
          <TextField
            label='Observation'
            placeholder='Observation'
            helperText=''
            fullWidth
            margin='normal'
            variant='outlined'
            name='observation'
            value={observation}
            onChange={(e) => onChange(e)}
          />
        </Grid>
        <Grid item xs={12} sm={12} lg={10}>
          <Button
            variant='contained'
            color='primary'
            size='large'
            onClick={send}
          >
            Valider la mise a jour de la Vehicule
          </Button>
        </Grid>
      </Grid>
    </div>
  );
};

VerticalLinearStepper.propTypes = {
  setAlert: PropTypes.func.isRequired,
};
export default connect(null, { setAlert })(VerticalLinearStepper);
